/*@import "includes/font-awesome";*/
body {
  font-family: 'Roboto Condensed', Arial, Helvetica, sans-serif;
}

nav ul {
  padding: 0 20px;
}

nav ul li {
  list-style-type: none;
  display: inline-block;
  margin-right: 2em;
}

nav ul li a {
  color: #006a76;
}

html {
  color: #333;
  font-size: 1.3em;
  line-height: 1.4;
  fhei-family: 'Roboto Condensed', sans-serif;
}

html header {
  width: 100vw;
  background-size: 100%;
}

html h1 {
  font-family: 'Sacramento', cursive;
  font-size: 5rem;
  color: #009598;
}

html h1, html h2, html footer, html #contact,
html #home,
html #portfolio {
  margin: 20px;
}

html #contact,
html #home,
html #portfolio {
  margin-bottom: 50px;
}

html #contact p,
html #home p,
html #portfolio p {
  max-width: 70vw;
}

html #contact a {
  font-size: 76px;
  margin-right: 16px;
}

html #portfolio a {
  display: block;
  margin-bottom: 1em;
}

html a {
  color: #009598;
  text-decoration: none;
}

html a:hover {
  color: #555;
  cursor: pointer;
}

html a:focus {
  text-shadow: #73C6B6;
}

html p a {
  color: #006a76;
}

html #portfolio .resume-list li {
  display: block;
  margin-bottom: 32px;
}

html #portfolio .resume-list li li {
  margin-bottom: 0;
}

html #portfolio .resume-list li li:first-child {
  text-transform: uppercase;
}

html footer {
  display: flex;
  font-size: .8em;
}

html footer svg {
  stroke-width: 36px;
  height: 36px;
  align-content: center;
  margin: -5px 5px 0 5px;
}
